.modal2 {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  color: white;
  border-radius: 6px;
  background-color: #cc0200;
  padding: 1rem;
  text-align: center;
  width: 24%;
  height: 40%;
  z-index: 1000;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.centered {
  top: 40%;
  left: 80%;
  margin-top: 2%;
  margin-left: 37%;
  border-radius: 10px;
  
}

.image {
  max-width: 300px;
  max-height: 50px;
  margin-top:5px;
}

.headblock {
  width: 100%;
  margin-top: 50px;
  background-color: #cc0200;
}

.topsection {
  background-color: #cc0200;
}

.description {
  margin-left: 50px;
  margin-top: -310px;
  border-color: #cc0200;
}

.profile {
  width: 30%;
  height: 20%;
  border-radius: 10px;
  margin-top: -5%;
  margin-left: 1%;
  border: 1px solid black;
}

.block {
  width: 600px;
  margin-top: 685px;
  margin-left: 441px;
  display: block;
  border-radius: 8px;
  border: none;
  background-color: #cc0200;
  color: white;
  padding: 14px 28px;
  font-family: 'Akshar', sans-serif;
  font-size: 30px;
  text-align: center;
}

.class {
  font-family: 'Akshar', sans-serif;
  font-size: 30px;
  margin-left: 1%;
  color: #cc0200;
}

.information {
  margin-left: 1%;
  font-size: 20px;
}

.bold {
  font-family: 'Akshar', sans-serif;
  font-size: 20px;
  margin-left: 1%;
  font-weight: bold;
}

.slider {
  background-color: #cc0200;
  top: -12%;
  left: 37%;
  margin-top: -13%;
  margin-left: 0%;
  border-radius: 15px;
  width: 58%;
  height: 205px;
  border: 1px solid black;
}

.inslider {
  font-size: 750%;
  text-align: center;
  color: aliceblue;
  margin-top: 4%;
  max-height: 125px;
}

.inslider2 {
  font-size: 400%;
  text-align: center;
  font-weight: 600;
  color: aliceblue;
  margin-top: 9%;
  max-height: 125px;
}

.remote {
    font-size: 500%;
    text-align: center;
    color: aliceblue;
    margin-top: 0%;
    background-color: #cc0200;
}

.mdlbutton {
  margin-top: 10%;
  left: 50%;
  background-color: #cc0200;
  display:inline-block;
  padding:0.35em 1.2em;
  border:0.1em solid #FFFFFF;
  border-radius:0.12em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:white;
  text-align:center;
  transition: all 0.2s;
}

.backdrop {
  position: fixed;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.bkgcolor {
  background-color: rgb(241, 240, 240);
  width: 100%;
  height: 100vh;
}

.button1 {
  transform: translate(500%, 8%);
  background-color: #cc0200;
  display:inline-block;
  padding:0.35em 1.2em;
  border:0.1em solid #FFFFFF;
  border-radius:0.12em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:white;
  text-align:center;
  transition: all 0.2s;
  }

  .button1:hover{
  color:#cc0200;
  background-color:#FFFFFF;
  }

  @media all and (max-width:30em){
  a.button1{
  display:block;
  margin:0.4em auto;
  }
}

